import axe, {type AxeResults, type Result} from 'axe-core'
import customRules from '../github/axe-custom-rules'
import {html, render} from '@github-ui/jtml-shimmed'

const component = document.querySelector('[data-component-output]')
const panel = parent.document.querySelector('[data-accessibility-panel]') as HTMLElement
const panelButton = parent.document.querySelector(
  'button[x-ref="accessibility"][data-component="tabs-tab"]',
) as HTMLElement

axe.configure(customRules)
if (component && panel && panelButton) {
  axe.run(component, (err: Error, results: AxeResults) => {
    if (err) throw err

    const passes = results.passes
    const violations = results.violations
    const incompletes = results.incomplete
    let violationCount = 0

    if (violations.length) {
      violationCount += violations.length
      renderResultSet(violations, 'Violations:', panel.querySelector('[data-results="violations"]')!)
    }

    if (incompletes.length) {
      violationCount += incompletes.length
      renderResultSet(incompletes, 'Incomplete:', panel.querySelector('[data-results="incompletes"]')!)
    }

    if (passes.length) {
      renderResultSet(passes, 'Passes:', panel.querySelector('[data-results="passes"]')!)
    }

    if (violationCount) {
      // eslint-disable-next-line i18n-text/no-en
      panelButton.textContent = `Accessibility (${violationCount})`
    }
  })
}

function renderResultSet(results: Result[], title: string, container: HTMLElement) {
  render(
    html`<div class="p-1">
      <strong>${title}</strong>
      ${results.map(result => {
        return html`<details class="hover:bg-lookbook-nav-item-hover">
            <summary class="p-1 cursor-pointer">${result.help}</summary>
            ${
              result.helpUrl
                ? html`<div style="margin-left: 25px">
                    <a href="${result.helpUrl}" target="_blank">More information</a>
                  </div>`
                : ''
            }
            <ul style="list-style-type: disc; padding: revert; margin-top: 0;">
              ${result.nodes.map(node => {
                return html`<li>
                  ${node.failureSummary ? html`<pre>${node.failureSummary}</pre>` : ''}
                  <code>${node.html}</code>
                </li>`
              })}
          </details>`
      })}
    </div>`,
    container,
  )
}
